import Vue from 'vue'
import Router from 'vue-router'

import Home from './components/home/Home.vue'

import SinglePost from './components/SinglePost/Index.vue'

import AuthorProfile from './components/AuthorProfile/index.vue'

import SingleCategory from './components/SingleCategory/index.vue'

Vue.use(Router)


export default new Router({
    
    routes:[

         {path: '/', component: Home},
         {path: '/post', component: SinglePost},
         {path: '/author', component: AuthorProfile},
         {path: '/category', component: SingleCategory},
    ]
})